import { Checkbox, Table } from 'antd';
import React from 'react';
import { useState, useEffect } from 'react';



// const success = () => {
//     message.success('success');
// };

const data = [
    {
        key: '1',
        name: 'sunday'
    },
    {
        key: '2',
        name: 'monday'
    },
    {
        key: '3',
        name: 'tuesday'
    },
    {
        key: '4',
        name: 'wednesday'
    },
    {
        key: '5',
        name: 'thursday'
    },
    {
        key: '6',
        name: 'friday'
    },
    {
        key: '7',
        name: 'saturday'
    },
];
const Home = () => {


    const [checkedItems, setCheckedItems] = useState([])

    useEffect(() => {
        const items = JSON.parse(localStorage.getItem('items'));
        if (items) {

            setCheckedItems(items);
        }
    }, []);


    const hours = [...Array(16).keys()].map((x) => (
        {

            title: x + 8 + (8 + x >= 12 ? ":00 PM" : ":00 AM"),
            dataIndex: 'key',
            key: x,
            render: (_, dayOfWeek) => <Checkbox
                checked={checkedItems.findIndex(item => dayOfWeek.name === item.dayOfWeek && item.hour === x + 8) >= 0}
                onChange={e => {
                    console.log(e.target.checked, dayOfWeek, x + 8)

                    e.target.checked ?
                        setCheckedItems([...checkedItems, { dayOfWeek: dayOfWeek.name, hour: x + 8 }]) :
                        setCheckedItems(checkedItems.filter(item => !(dayOfWeek.name === item.dayOfWeek && item.hour === x + 8)))
                }} />


        }
    ))


    const columns = [
        {
            title: ' ',
            width: 120,
            dataIndex: 'name',
            key: 'name',
            fixed: 'left',

        },

        ...hours

    ];

    console.log(checkedItems)

    return (



        <div>
            <Table columns={columns} dataSource={data} scroll={{ x: 1300, }} />

            <button onClick={() => localStorage.setItem('items', JSON.stringify(checkedItems))}>Submit</button>

        </div>
    )
};


export default Home;